import React from 'react'
import { Box, Button } from '@mui/material'
import { useNavigate } from 'react-router-dom' // useNavigate

function Choice () {
  const navigate = useNavigate() // useNavigate

  // 
  const navigateToFreeAssessment = () => {
    navigate('/home/assessment/free')
  }

  // 
  const navigateToPaidAssessment = () => {
    navigate('/home/assessment/paid')
  }

  return (
    <Box>
      <Button onClick={navigateToFreeAssessment} variant="contained"></Button>
      <Button onClick={navigateToPaidAssessment} variant="contained" color="primary"></Button>
    </Box>
  )
}

export default Choice
