import React from 'react'
import { Box } from '@mui/material'
import NewsSection from './NewsSection'

function NewsList () {
  const newsData = [
    {
      title: '',
      articles: [
        { title: '', url: 'https://baidu.com' },
        { title: '', url: '#' },
        { title: '', url: '#' },
        { title: '', url: '#' },
        { title: '', url: '#' },
        { title: '2024', url: '#' },
        { title: '', url: '#' },
        { title: '', url: '#' },
      ],
    },
    {
      title: '/',
      articles: [
        { title: '5G', url: '#' },
        { title: '5G', url: '#' },
        { title: '', url: '#' },
        { title: '', url: '#' },
        { title: '', url: '#' },
        { title: '', url: '#' },
        { title: '', url: '#' },
        { title: '', url: '#' },
      ],
    },
    {
      title: '',
      articles: [
        { title: '', url: '#' },
        { title: '', url: '#' },
        { title: '', url: '#' },
        { title: '', url: '#' },
        { title: '', url: '#' },
        { title: '', url: '#' },
        { title: '', url: '#' },
        { title: '', url: '#' },
      ],
    },
  ]

  return (
    <Box sx={{ '& > *:not(:last-child)': { marginBottom: '50px' } }}>
      {newsData.map((section, index) => (
        <NewsSection key={index} title={section.title} articles={section.articles} />
      ))}
    </Box>
  )
}

export default NewsList
